.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
@media screen and (max-width: 36rem) {
  .container {
    max-width: 31.875rem;
  }
}
@media screen and (max-width: 48rem) {
  .container {
    max-width: 43.125rem;
  }
}
@media screen and (max-width: 62rem) {
  .container {
    max-width: 58.125rem;
  }
}
@media screen and (max-width: 75rem) {
  .container {
    max-width: 69.375rem;
  }
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body,
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.container {
  width: 100%;
  box-sizing: border-box;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Bold.eot");
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/Montserrat/Montserrat-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Regular.eot");
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat/Montserrat-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Medium.eot");
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat/Montserrat-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Playfair Display";
  src: url("../fonts/Playfair/PlayfairDisplay-Regular.woff2") format("woff2") url("../fonts/Playfair/PlayfairDisplay-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
body,
html {
  font-family: "Montserrat", sans-serif;
  font-size: 100%;
  text-wrap: balance;
}

.header {
  padding: 1.875rem 0 1.5625rem;
  background-color: #fcfdf5;
}
.header-title {
  font-size: 0.875em;
  font-weight: 600;
  text-align: center;
}

.first-section {
  padding: 0.75rem 0 1rem;
}
.first-section .section-content {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-top: 1.25rem;
  box-sizing: border-box;
}
.first-section .col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
  width: calc(33.3333333333% - 1.875rem);
  word-wrap: break-word;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
@media screen and (max-width: 48rem) {
  .first-section .col {
    width: calc(50% - 1.875rem);
  }
}
@media screen and (max-width: 36rem) {
  .first-section .col {
    width: calc(100% - 1.875rem);
  }
}
@media screen and (max-width: 36rem) {
  .first-section .col {
    margin-top: 0.9375rem;
  }
  .first-section .col:first-child {
    margin-top: 0;
  }
}
.first-section .col:nth-child(1) {
  height: 15.625rem;
  background-color: cadetblue;
}
.first-section .col:nth-child(2) {
  height: 20rem;
  background-color: moccasin;
}
.first-section .col:nth-child(3) {
  height: 10rem;
  background-color: darksalmon;
}
@media screen and (max-width: 48rem) {
  .first-section .col:nth-child(3) {
    margin-top: -1.875rem;
  }
}
@media screen and (max-width: 36rem) {
  .first-section .col:nth-child(3) {
    margin-top: 0.9375rem;
  }
}
.first-section .col:nth-child(4) {
  height: 7.4375rem;
  background-color: lightblue;
}
.first-section .col-txt {
  margin-top: 0.75rem;
  margin-bottom: 0.5rem;
}
.first-section .sect-title {
  font-size: 1.375em;
  font-weight: 600;
  margin: 1.25rem 0 1.25rem;
  text-align: center;
}

.footer {
  padding: 0.75rem 0 2rem;
  background-color: #131313;
}
.footer-title {
  font-size: 0.875em;
  font-weight: 600;
  padding: 1.375rem 0 0.75rem;
  text-align: center;
  color: #fcfcfc;
}